/**
 * Standalone build entry for UEditor Plus Designer
 * This file creates a global API for using the designer in vanilla HTML/JS
 */
import { createApp, h, ref } from 'vue'
import Designer from './components/Designer.vue'
import type { DesignerConfig } from './types'

// Import styles
import './styles/index.scss'

interface DesignerInstance {
  mount: (container: string | HTMLElement) => void
  unmount: () => void
  getContent: () => string
  setContent: (content: string) => void
  on: (event: string, callback: Function) => void
  off: (event: string, callback: Function) => void
}

/**
 * Create a designer instance
 */
function createDesigner(config?: DesignerConfig): DesignerInstance {
  let app: any = null
  let designerRef: any = null
  const eventHandlers: Record<string, Function[]> = {
    ready: [],
    change: [],
    confirm: []
  }

  return {
    /**
     * Mount the designer to a container
     */
    mount(container: string | HTMLElement) {
      const containerEl = typeof container === 'string' 
        ? document.querySelector(container) 
        : container

      if (!containerEl) {
        throw new Error(`Container not found: ${container}`)
      }

      // Create Vue app
      app = createApp({
        setup() {
          designerRef = ref(null)

          const handleReady = () => {
            eventHandlers.ready.forEach(cb => cb())
          }

          const handleChange = (content: string) => {
            eventHandlers.change.forEach(cb => cb(content))
          }

          const handleConfirm = (content: string) => {
            eventHandlers.confirm.forEach(cb => cb(content))
          }

          return () => h(Designer, {
            ref: designerRef,
            config: config || {},
            isIframe: false,
            onReady: handleReady,
            onChange: handleChange,
            onConfirm: handleConfirm
          })
        }
      })

      app.mount(containerEl)
    },

    /**
     * Unmount the designer
     */
    unmount() {
      if (app) {
        app.unmount()
        app = null
        designerRef = null
      }
    },

    /**
     * Get editor content
     */
    getContent() {
      if (designerRef?.value) {
        return designerRef.value.getContent()
      }
      return ''
    },

    /**
     * Set editor content
     */
    setContent(content: string) {
      if (designerRef?.value) {
        designerRef.value.setContent(content)
      }
    },

    /**
     * Add event listener
     */
    on(event: string, callback: Function) {
      if (eventHandlers[event]) {
        eventHandlers[event].push(callback)
      }
    },

    /**
     * Remove event listener
     */
    off(event: string, callback: Function) {
      if (eventHandlers[event]) {
        const index = eventHandlers[event].indexOf(callback)
        if (index > -1) {
          eventHandlers[event].splice(index, 1)
        }
      }
    }
  }
}

// Export to global window object
if (typeof window !== 'undefined') {
  (window as any).UEditorPlusDesigner = {
    createDesigner
  }
}

export { createDesigner }
export default { createDesigner }
